<template>
    <div  v-loading="loading">
        <el-card>
            {{msg}}
        </el-card>
    </div>
</template>

<script>
export default {
    data () {
        return {
            msg: '主页预留',
            loading: true
        }
    },
    mounted(){
        this.loading = false;
    }
}
</script>
</style>



